
let baseUrl = '';
if (process.env.NODE_ENV == 'development') {

    baseUrl='/api'
	

	baseUrl='http://www.ls.xyz/'

	
} else {
    baseUrl = 'https://laihu.baogongxia.com' // 生产环境
}

function request(url,method,data,header={}){
  uni.showLoading({
    title: '加载中...'
  })
  return new Promise((resolve,reject) => {
    uni.request({
      url: baseUrl + url,
      method: method,
      data: data,
      header: {
        'content-type': 'application/json'
      },
      success: function(resp) {
        uni.hideLoading();
		if (resp.data.code == 0) {
			resolve(resp.data);
		
		}
		else if (resp.data.code == 2) {
			uni.navigateTo({
				url:'login?type=back'
			});
			
		}
		else {
			
			uni.showToast({
				title: resp.data.message,
				duration: 2000,
				icon: 'none'
			});
		}
      },
      fail: function(resp) {
        uni.hideLoading();
        reject(resp);
      },
    })
  })
}

// 封装get方法
function get(obj) {
  return request(obj.url,'GET',obj.data)
}
// 封装post方法
function post(obj) {
  return request(obj.url,'POST',obj.data)
}

export default {
  request,
  get,
  post,
  baseUrl
}
